$cell-size: 100px;
$gap-size: 1px;
$calendar-padding: 10px;
$calendar-size: 7 *( $cell-size + $gap-size);
$week:
  "mon" 0,
  "thu" ($cell-size + $gap-size),
  "wen" ($cell-size + $gap-size) * 2,
  "thur" ($cell-size + $gap-size) * 3,
  "fri" ($cell-size + $gap-size) * 4,
  "sat" ($cell-size + $gap-size) * 5,
  "sun" ($cell-size + $gap-size) * 6;
